
import Scroller from 'vux/src/components/scroller/index.vue'

import {API,getQuery} from '../../services';


export default {
components: {
Scroller,
},
data() {
return {
/* 当前被选中列表 */
currentType: 0,
/* 分类数据集合 */
typeList: [],
}
},
methods: {
/* 路由回退 */
routerback(){
this.$router.goBack();
},
/* 列表点击切换函数 */
typeselect(index) {
for (let item of this.typeList) {
item.click = false;
}
this.currentType=index;
this.typeList[index].click = true;
var data = this.typeList;
this.typeList = null;
this.typeList = data;
},
/* 跳转到搜索页面 */
gosearch(){
window.location.href="/#/search";
},
/* 对象初始化前数据筛选 */
dataFormat(data){
for(let i= 0;i<data.length;i++){
if(data[i].count==0){
data.splice(i,1);
i--;
}else{
for(let j= 0,twoData=data[i].children;j<twoData.length;j++){
if(twoData[j].count==0){
twoData.splice(j,1);
j--;
}else{
for(let k= 0,threeData=twoData[j].children;k<threeData.length;k++){
if(threeData[k].count==0){
threeData.splice(k,1);
k--;
}
}
}
}
}
}
},
/* 列表点击函数 */
goSearch(id,name){
window.location.href="/#/goodsList?categoryId="+id+"&name="+name;
}
},
mounted(){
/* 获取整体分类数据，三重结构，去除第二重 */
API.main.goodsCategory().then((Response)=>{
let typeList=Response.body.data;
this.dataFormat(typeList);
this.typeList=typeList;
this.typeList[0].click=true;
});
}
}



